<template>
  <div class="live-container">
    <div class="live-banner fl">
      <img src="../../assets/live_tip_banner.png" alt>
    </div>
    <div class="live-content fl">
      <div class="live-head">
        <div class="more">
         <!--  <span class="text" @click="LinkMore">更多教程</span> -->
         <!--  <span class="iconfont icon-jiantou"></span> -->
        </div>
        <div class="clear"></div>
      </div>
      <div class="live-box">
        <div class="empty-tip" v-if="datas.length == 0">暂无数据</div>
        <live-advance-card
          v-for="(advance,index) in datas"
          :key="index"
          :class="{'mgr0':(index+1)%4 == 0}"
          :item="advance"
        ></live-advance-card>
      </div>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
import liveAdvanceCard from "./liveAdvanceCard.vue";
export default {
  components: {
    liveAdvanceCard
  },
  props: {
    datas: Array
  },
  methods: {
    LinkMore() {}
  }
};
</script>
<style lang="less" scoped>
.live-container {
  width: 100%;
  height: 200px;
  background: #fff;
  .live-banner {
    width: 160px;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .live-content {
    width: calc(100% - 160px);
    padding: 10px 25px;
    .live-box {
      width: 100%;
    }
  }
}
</style>



