<template>
  <div class="index-resource-container">
    <div class="content-header">
      <div class="title">
        <span class="text">{{title}}</span>
        <span class="tag" v-if="tag != ''">{{tag}}</span>
        <slot name="tag"></slot>
      </div>
      <div class="more">
        <span class="text" @click="LinkMore()">{{moreText}}</span>
        <span class="iconfont icon-jiantou"></span>
      </div>
      <div class="clear"></div>
    </div>
    <div class="context" v-loading="loading">
      <div class="empty-tip" v-if="list && list.length == 0">暂无数据</div>
      <course-item
        v-for="(item,index) in list"
        :key="index"
        :item="item"
        :is-live="isLive"
        :class="{'mgr0':(index+1)%5 == 0}"
      />
      <div class="clear"></div>
    </div>
  </div>
</template>
<script>
import courseItem from "../courseItem";
export default {
  props: {
    title: String,
    tag: {
      type: String,
      default: ""
    },
    list: Array,
    loading: Boolean,
    isLive: {
      type: Boolean,
      default: true
    },
    moreText: {
      type: String,
      default: "更多资源"
    }
  },
  components: {
    courseItem
  },
  methods: {
    LinkMore() {
      this.$emit("LinkTo", "");
    }
  }
};
</script>
<style lang="less" scoped>
.index-resource-container {
  width: 1200px;
  margin: 50px auto;
  background: #fff;
  padding: 15px 25px;
  // 头部
  .content-header {
    width: 100%;
    .title {
      float: left;
      .text {
        font-size: 1.25rem;
        font-weight: 500;
      }
      .tag {
        font-size: 0.8rem;
        color: #bbb;
        margin-left: 8.8px;
      }
    }
  }
  .context {
    width: 100%;
  }
}
</style>



