export function getOffsetTopByClass (className, index = 0) {
    let doms = document.getElementsByClassName(className)
    if (doms.length > 0) {
        return doms.item(index).offsetTop
    }else {
        console.error('找不到' + className)
    }
}

export function setAttrById (id = '', type = '', value = '') {
    let dom = document.getElementById(id)
    if (dom) {
        dom.setAttribute(type, value)
    }else {
        console.error('找不到' + id)
    }
}


