<template>
  <div class="live-advance-card" v-if="item.visible">
    <div class="title">{{item.title}}</div>
    <div class="box">
      <span class="half-width">教师：{{item.teacher}}</span>
      <span class="half-width">时间：{{item.time}}</span>
      <span>教室：{{item.room}}</span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    item: Object
  }
};
</script>
<style lang="less" scoped>
.live-advance-card {
  width: 233px;
  margin-right: 18px;
  float: left;
  margin-top: 8px;
  .title {
    width: 100%;
    height: 80px;
    background: #e8f2f9;
    padding: 20px;
    font-weight: 550;
    text-align: left;
  }
  .box {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    font-size: 0.9rem;
    margin-top: 15px;
  }
}
</style>



