<template>
  <div class="total-infor-bar">
    <div class="infor-bar" v-for="(bar,i) in datas" :key="i" :class="{'n-border':i==4}">
      <div class="value blue-text">{{bar.value}}</div>
      <div class="hori-line"></div>
      <div class="text">{{bar.text}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    datas: Array
  }
};
</script>
<style lang="less" scoped>
.total-infor-bar {
  width: 1200px;
  height: 160px;
  background: #fff;
  box-shadow: 0 5px 8px #ddd;
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
  .infor-bar {
    width: 20%;
    height: 100%;
    border-right: 1px solid #dedede;
    .value {
      font-size: 2.6rem;
    }
    .hori-line {
      width: 23px;
      height: 1px;
      background: #ccc;
      margin: 0 auto;
      margin-bottom: 8px;
    }
    .text {
      color: #838383;
    }
  }
}
</style>



