    getRouteDatas() {
      if (this.$route.query.selectData) {
        let receivearray = JSON.parse(this.$route.query.selectData);
        receivearray.forEach(element => {
          if (element.title === "搜索") {
            this.key = element.text;
          }
        });
      }
      this.fetchNewResource();
      this.fetchHotResource();
