    scrollHandler() {
      this.scrollY = window.scrollY;
      this.lazyload();
    },
    lazyload() {
      if (
        !this.starFetchHot &&
        getOffsetTopByClass("hot-resource") + 36 <
          window.innerHeight + this.scrollY
      ) {
        // this.fetchRanks();
        this.fetchHotResource();
        // console.log("加载热门");
      }
      if (
        !this.starFetchLink &&
        getOffsetTopByClass("friend-link") + 36 <
          window.innerHeight + this.scrollY
      ) {
        this.fetchLink();
        // console.log("加载友链");
      }
