      const res = await http.get(api.getConfigInfo, "");
      if (res.Result) {
        cookie.set(api.cookie.video_config, JSON.stringify(res.Datas));
      }
    },
    LoadScreen() {
      this.fetchfilterData([
        api.getAllCampusesData,
        {
          token: token //http.getToken()
        },
        0
      ]);
      //大楼
      this.fetchfilterData([
        api.getBuildingDatasByCT,
        {
          token: token, //http.getToken(),
          cids: this.cids,
          type: 9
        },
        1
      ]);
      //楼层
      this.fetchfilterData([
        api.getFloorByBuilding,
        {
          token: token, //http.getToken(),
          cids: this.cids,
          type: 9,
          bids: this.bids
        },
        2
      ]);
      //课程、专业、教师
      this.fetchfilterData([
        api.GetRecScreenCondition,
        {
          date: "1",
          cids: this.cids,
          bids: this.bids,
          fs: this.fs,
          token: token //http.getToken()
        },
        3
      ]);
      //教室
      this.fetchfilterData([
        api.getDatasByCampusBuildingFloors,
        {
          cids: this.cids,
          bids: this.bids,
          floors: this.fs,
          type: 9,
          token: token //http.getToken()
        },
        4
      ]);
    },
    fetchfilterData: async function(paramsArr) {
      let url = paramsArr[0];
      let params = paramsArr[1];
      let type = paramsArr[2];
      let res = await http.get(url, params);
      if (res.Datas) {
        switch (type) {
          case 1:
            //教学楼
            this.filterLines[1].children = [];
            res.Datas.forEach(element => {
              this.filterLines[1].children.push({
                active: false,
                text: element.BuildingName,
                code: "bids",
                id: element.BuildingID
              });
            });
            break;
          case 2:
            //楼层
            this.filterLines[2].children = [];
            res.Datas.forEach(element => {
              this.filterLines[2].children.push({
                active: false,
                text: "第" + element + "层",
                code: "fs",
                id: element
              });
            });
            break;
          case 3:
            //专业、课程
            this.lessonList = [];
            this.gradeList = [];
            this.teacherList = [];
            if (res.Datas.Title) {
              res.Datas.Title.forEach(element => {
                this.filterLines[4].children.push({
                  active: false,
                  text: element,
                  code: "less",
                  id: element
                });
              });
            }
            if (res.Datas.ZYNJ) {
              this.filterLines[0].children = [];
              res.Datas.ZYNJ.forEach(element => {
                this.filterLines[0].children.push({
                  active: false,
                  text: element,
                  code: "zynj",
                  id: element
                });
              });
            }
            if (res.Datas.Teachers) {
              res.Datas.Teachers.forEach(element => {
                this.teacherList.push({
                  text: element,
                  code: "teach",
                  id: element
                });
              });
            }
            break;
          case 4:
            //教室
            this.clsroomList = [];
            res.Datas.forEach(element => {
              this.filterLines[3].children.push({
                active: false,
                text: element.ClsRoomName,
                code: "clsids",
                id: element.ClsRoomID
              });
            });
            break;
          default:
            break;
        }
      }
    },
    async fetchTaskCount() {
      const res = await http.get(api.getRecTaskCount, {});
      if (res.Result) {
        this.infors[1].value = res.count[0];
        this.infors[2].value = res.count[1];
        this.infors[3].value = res.count[2];
        this.infors[4].value = res.count[3];
