        res.Datas.forEach(element => {
          this.bannerImg =
            downloadURL + "?filename=" + element.SCID + "&fileType=7";
        });
      }
    },
    async fetchLink() {
      this.starFetchLink = true;
      let params = {
        key: "",
        sfield: "",
        sdir: "",
        estate: "all",
        pno: 1
      };
      const res = await http.get(api.getLink, params);
      this.linkLoading = false;
      if (res.Result) {
        this.linkList = [
          {
            name: "睿和科技",
            link: "www.fjrh.cn/",
            img: "../../../static/link.png"
          }
        ];
        let num = 0;
        let downloadURL = await api.GetDownloadFileURL();
        res.Datas.forEach(element => {
          if (num < 3) {
            this.linkList.push({
              name: element.LinkName,
              link: element.LinkURL,
              img:
                downloadURL +
                "?filename=" +
                element.LinkID +
                "&fileType=8&platFormType=1"
            });
          }
          num++;
        });
        // 移除滚动监听
        window.removeEventListener("scroll", this.scrollHandler);
      } else {
        this.starFetchLink = false;
      }
    },
    async fetchNewResource() {
      let params = {
        key: this.key,
        courses: "",
        disciplines: "",
        grades: "",
        pno: 1,
        psize: 10,
        sfield: "m.MaterialCreateDate",
        sdir: 1
      };
      const res = await http.get(api.getMaterialInfo, params);
      this.newLoading = false;

      this.NewMaterialList = await this.getList(res);
    },
    async fetchLive() {
      let params = { psize: 4 };
      const res = await http.get(api.getLive, params);

      this.topLoading = false;
      if (res.Result) {
        this.LiveList = [];
        let downloadURL = await api.GetDownloadFileURL();
        res.Datas.forEach(element => {
          this.LiveList.push({
            title: element.Key.CSTitle,
            imgSrc: downloadURL + "?filename=resources&fileType=101",
            label: element.Name,
            teacher: element.TeacherName,
            id: element.Key.RECID,
            lesson: formatDate(element.Key.StartTime, "hh:MM:ss"),
            class: element.ClsName,
            date: formatDate(element.Key.StartTime, "yyyy-MM-dd"),
            roomname: element.RoomName,
            time: formatDate(element.Key.StartTime, "hh:MM:ss"),
            clsroomid: element.Key.ClsRoomID,
            recid: element.Key.RECID
          });
        });
        this.liveNums = res.RowsCount;
      }
    },
    async fetchUnBeginningLive() {
      let params = { psize: 100 };
      const res = await http.get(api.getUnBeginningTask, params);
      this.topLoading = false;
      console.log(res.Result);
      if (res.Result) {
        this.liveAdvances = [];
        res.Datas.forEach(element => {
          let ScheduleInfo = null;
          if (element.ScheduleInfo)
            ScheduleInfo = JSON.parse(element.ScheduleInfo);

          let RTInfo = null;
          if (element.RTInfo) RTInfo = JSON.parse(element.RTInfo);

          this.liveAdvances.push({
            title:
              ScheduleInfo &&
              ScheduleInfo.BaseInfo &&
              ScheduleInfo.BaseInfo.CourseName
                ? ScheduleInfo.BaseInfo.CourseName
                : element.RTName,
            teacher:
              ScheduleInfo && ScheduleInfo.Teachers
                ? ScheduleInfo.Teachers
                : "",
            room: RTInfo.ClsRoom.ClsRoomName,
            time: formatDate(element.RTStart, "hh:MM:ss"),
            recid: element.RTID,
            clsroomid: RTInfo.ClsRoom.ClsRoomID,
            visible: false
          });
        });
      }
    },
    async fetchHotResource() {
      this.starFetchHot = true;
      let params = {
        key: this.key,
        courses: "",
        disciplines: "",
        grades: "",
        pno: 1,
        psize: 10,
        sfield: "m.Visits",
        sdir: 1
      };
      const res = await http.get(api.getMaterialInfo, params);
      this.hotLoading = false;
      if (res.type) {
        this.starFetchHot = false;
      } else {
        this.MaterialList = await this.getList(res);
      }
    },
    async getList(res) {
      if (res.Result) {
        let list = [];
        let downloadURL = await api.GetDownloadFileURL();
        res.Datas.forEach(element => {
          let ScheduleInfo = {};

          try {
            if (element.ScheduleInfo)
              ScheduleInfo = JSON.parse(element.ScheduleInfo);
          } catch (e) {}

          list.push({
            title:
              ScheduleInfo && ScheduleInfo.CourseName
                ? ScheduleInfo.CourseName
                : element.RTName,
            imgSrc:
              downloadURL + "?filename=" + element.RTID + "&fileType=102",
            label: element.RTName,
            recid: element.RTID,
            teacher: ScheduleInfo.Teachers,
            id: element.MaterialID,
            watcher:
              element.Visits && element.Visits == "" ? 0 : element.Visits,
            lesson:
              element.RTStart && element.RTEnd
                ? formatDate(element.RTStart, "hh:mm") +
                  "-" +
                  formatDate(element.RTEnd, "hh:mm")
                : "",
            class: ScheduleInfo.Classes,
            date: formatDate(new Date(element.MaterialCreateDate), "yyyy-MM-dd"),
            roomname: element.ClsRoomName
          });
        });
        return list;
      }
    },
    // async fetchRanks() {
    //   let params = { top: 6 };
    //   const res = await http.get(api.getResourceRanks, params);
    //   if (res.Result) {
    //     this.rankList = [];
    //     res.Datas.forEach(element => {
