      let paramlist = [];
      paramlist.push({
        title: title,
        id: id,
        text: text
      });
      let param = JSON.stringify(paramlist);
      this.$router.push({
        name: "course",
        query: {
          selectData: param,
          sfield: sfield,
          sdir: 1
        }
      });
    },
    selectedKeyword(item) {
      if (item.choosed) item.choosed = false;
      else item.choosed = true;
      let paramsdisciplines = "";
      let paramscourses = "";
      let paramsgrades = "";
      this.searchList.forEach(ele => {
        if (ele.choosed) {
          switch (ele.type) {
            case 1:
              paramsdisciplines += ele.id + ",";
              break;
            case 2:
              paramsgrades += ele.id + ",";
              break;
            case 3:
              paramscourses += ele.id + ",";
              break;
          }
        }
      });
      paramsdisciplines = paramsdisciplines.slice(
        0,
        paramsdisciplines.length - 1
      );
      paramsgrades = paramsgrades.slice(0, paramsgrades.length - 1);
      paramscourses = paramscourses.slice(0, paramscourses.length - 1);
      let params = {
        key: "",
        courses: paramscourses,
        disciplines: paramsdisciplines,
        grades: paramsgrades,
        pno: 1,
        psize: 6,
        sfield: "Visits",
        sdir: 1
      };
      this.fetchCourse(params);
    },
    async openSocket() {
      let signalrURL = await api.GetSignalrURL();
      conn = $.hubConnection(signalrURL);
      hub = conn.createHubProxy("TerminalHub");
      conn.qs = {
        token: token //http.getToken()
      };
      var $this = this;
      let downloadURL = await api.GetDownloadFileURL();
      hub.on("ReceiveALLRECStatus", function(jsonInfo) {
        var recList = JSON.parse(jsonInfo);
        let count = 0;
        let index = 0;
        $this.LiveList = [];
        recList.forEach(element => {
          if (element.LiveEstate == 1) {
            count++;
          }
          //刷新当前窗口未直播
          if (element.LiveEstate == 0) {
            $this.liveAdvances.forEach(ele => {
              if (ele.recid == element.RECID) {
                if ($this.liveAdvances) $this.liveAdvances.remove(ele);
              }
            });
          }
          //以singler里的数据作为直播推荐列表数据
          let key = $this.key;
          if (key != "") {
            if (
              (element.CourseName && element.CourseName.search(key) != -1) ||
              // (element.ZYNJ && element.ZYNJ.search(key) != -1) ||
              (element.Teachers && element.Teachers.search(key) != -1) ||
              (element.Classes && element.Classes.search(key) != -1) ||
              (element.ClsRoomName && element.ClsRoomName.search(key) != -1)
            ) {
              $this.LiveList.push({
                title: element.CourseName,
                imgSrc:
                  downloadURL + "?filename=" + element.ID + "&fileType=101",
                // label: element.ZYNJ,
                teacher: element.Teachers,
                id: element.ID,
                lesson: formatDate(element.StartTime, "hh:MM:ss"),
                class: element.Classes,
                date: formatDate(element.StartTime, "yyyy-MM-dd"),
                roomname: element.ClsRoomName,
                time: formatDate(element.StartTime, "hh:MM:ss"),
                clsid: element.ClsRoomID,
                recid: element.ID
              });
              index++;
            }
          } else {
            if (index < 5 && element.LiveEstate == 1) {
              $this.LiveList.push({
                title: element.CourseName,
                imgSrc:
                  downloadURL + "?filename=" + element.ID + "&fileType=101",
                // label: element.ZYNJ,
                teacher: element.Teachers,
                id: element.ID,
                lesson: formatDate(element.StartTime, "hh:MM:ss"),
                class: element.Classes,
                date: formatDate(element.StartTime, "yyyy-MM-dd"),
                roomname: element.ClsRoomName,
                time: formatDate(element.StartTime, "hh:MM:ss"),
                clsid: element.ClsRoomID,
                recid: element.ID
              });
              index++;
            }
          }
        });

        for (var i = 0; i < 4; i++) {
          if ($this.liveAdvances[i]) {
            $this.liveAdvances[i].visible = true;
          }
        }
        $this.infors[0].value = count;
        // $this.infors[1].value = $this.liveAdvances.length;
        $this.liveNums = count;
      });
      conn
        .start(() => {
          /*  this.$notify({
            title: "开始",
            message: "正在开启实时状态同步",
            type: "info",
            duration: 2599
          }); */
        })
        .done(data => {
          hub.invoke("JoinGroup", "ALLREC");
          /* setTimeout(() => {
            this.$notify({
              title: "成功",
              message: "状态同步开启成功",
              type: "success",
              duration: 2599
            });
          }, 1520); */
        })
        .fail(err => {
          /* setTimeout(() => {
            conn.start();
            this.$notify.error({
              title: "错误",
              message: "状态同步开启失败"
            });
          }, 1520); */
        });
    }
  }
};
</script>
<style lang='less' scoped>
// 头部
.content-header {
  width: 100%;
  height: 36px;
  line-height: 36px;
  .title {
    float: left;
    .text {
      font-size: 1.25rem;
      font-weight: 550;
    }
    .tag {
      font-size: 0.8rem;
      color: #bbb;
      margin-left: 8.8px;
    }
  }
  .more {
    float: right;
    color: #aaa;
    font-size: 0.8rem;
    &:hover {
      cursor: pointer;
    }
  }
}
.teacher-recomend {
  width: 100%;
  margin-top: 28px;
  .teacher-box {
    width: 100%;
    padding-top: 18px;
    display: flex;
    justify-content: space-between;
  }
}
.friend-link {
  width: 100%;
  height: 171.5px;
  margin-top: 28px;
  .link-box {
    width: 100%;
    margin-top: 15.2px;
    .link-item {
      width: 280px;
      height: 123px;
      display: block;
      float: left;
      margin-right: 26px;
      .img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>



