<template>
  <div class="banner-filter" @mouseleave="outHandler($event)">
    <div class="filter-nav">
      <div
        class="filter-bar"
        v-for="(bar,i) in filterNavs"
        :key="i"
        :class="{'bar-active': bar.active,'n-border':(i+1)%3 == 0}"
        @mouseover="hoverHandler(bar,i)"
      >
        <i class="iconfont" :class="bar.icon"/>
        {{bar.text}}
        <i class="iconfont icon-jiantou"/>
      </div>
    </div>
    <transition name="el-fade-in-linear">
      <div v-show="showContent" class="filter-content">
        <div
          class="filter-bar"
          v-for="(bar,i) in filter"
          :key="i"
          :class="{'n-border': (i+1)%3==0 ||bar.index!=1}"
        >
          <div class="bar-title fl">{{bar.title}}</div>
          <div class="bar-content fl">
            <span
              class="bar-item"
              v-for="(item,j) in bar.children"
              :key="j"
              :class="{'item-active':item.active}"
              @click="itemHandler(bar, item)"
            >{{item.text}}</span>
          </div>
          <div class="clear"></div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  props: {
    filterNavs: Array,
    filterLines: Array
  },
  data() {
    return {
      showContent: false,
      filter: []
    };
  },
  methods: {
    hoverHandler(ele, i) {
      this.filterNavs.forEach(element => {
        if (ele == element) {
          this.$set(element, "active", true);
          this.$emit("navChange", element);
          this.filter = [];
          this.filterLines.forEach(e => {
            if (e.index === i) {
              this.filter.push(e);
            }
          });
        } else {
          this.$set(element, "active", false);
        }
      });
      this.showContent = true;
    },
    outHandler(event) {
      event.preventDefault();
      this.showContent = false;
      this.filterNavs.forEach(element => {
        this.$set(element, "active", false);
      });
      this.filterLines.forEach(element => {
        element.children.forEach(ele => {
          this.$set(ele, "active", false);
        });
      });
    },
    itemHandler(bar, item) {
      bar.children.forEach(element => {
        if (item === element) {
          this.$set(element, "active", !element.active);
          if (element.active) {
            this.$emit("itemChange", element);
          }
        } else {
          this.$set(element, "active", false);
        }
      });
      this.LinkMoreResource("", bar.title, item.id, item.text, item.code);
    },
    LinkMoreResource(sfield, title, id, text, code) {
      let paramlist = [];
      paramlist.push({
        title: title,
        id: id,
        code: code,
        text: text
      });
      let param = JSON.stringify(paramlist);
      this.$router.push({
        name: "course",
        params: {
          selectData: param,
          sfield: sfield,
          sdir: 1
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.banner-filter {
  width: 1200px;
  height: 300px;
  margin-bottom: 2px;
  z-index: 99;
  position: relative;
  .filter-nav {
    width: 180px;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    // border: 2px solid #167dd0;
    position: absolute;
    left: 0;
    top: 0;
    .filter-bar {
      border-bottom: 1px solid #6ea3c1;
      display: flex;
      justify-content: space-around;
      font-size: 1.36rem;
      align-items: center;
      cursor: pointer;
      .iconfont {
        font-size: 1.52rem;
      }
    }
    .bar-active {
      background: rgb(22, 125, 208, 0.8) !important;
    }
  }
  .filter-content {
    width: calc(100% - 180px);
    min-height: 300px;
    background: rgba(255, 255, 255, 0.9);
    padding: 0 20px;
    position: absolute;
    left: 180px;
    top: 0;
    box-shadow: 1px 2px 5px #eee;
    .filter-bar {
      border-bottom: 2px solid #8ccafd;
      padding: 15px 0;
      text-align: left;
      .bar-title {
        width: 77px;
        height: 100%;
        text-align: left;
        color: #50acfc;
        font-size: 0.8rem;
        padding-top: 5px;
      }
      .bar-content {
        width: calc(100% - 77px);
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        .bar-item {
          padding: 5px 12px;
          font-size: 0.8rem;
          white-space: nowrap;
          cursor: pointer;
        }
        .item-active {
          background: #2a9efc;
          color: #fff;
        }
      }
    }
  }
  .filter-bar {
    width: 100%;
    min-height: 100px;
  }
}
</style>



